@import url('../../public/tools/normalize.min.css');

:root {
	--font-main: 'Roboto', Arial, Helvetica, sans-serif;
	--font-second: 'Roboto', Arial, Helvetica, sans-serif;

	--font-size: 21px;
	--font-size-var1: clamp(24px, 3.69vw, 48px);
	--font-size-var2: clamp(15px, 1.615vw, 21px);
	--font-size-var2a: clamp(12px, 1.615vw, 21px);
	--font-size-var3: clamp(18px, 1.53vw, 20px);
	--font-size-var3a: clamp(14px, 1.53vw, 20px);
	--font-size-var4: clamp(12px, 1.615vw, 21px);

	--color-white: #ffffff;
	--color-black: #000000;
	--color-gray: #eef3f9;
	--color-blue: #2eaeff;
	--color-cyan: #39b3fe;
}

* {
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
	&:after {
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
	&:before {
		-webkit-box-sizing: border-box;
		box-sizing: border-box;
	}
}
body {
	background: var(--color-white);
	font-style: normal;
	font-weight: normal;
	font-family: var(--font-main);
	font-size: var(--font-size);
	color: var(--color-black);
}
a {
	text-decoration: none !important;
	color: inherit;
}
h1,
h2,
h3,
h4,
h5,
h6,
p {
	margin: 0;
	padding: 0;
	-webkit-font-feature-settings: 'ordn' on;
	font-feature-settings: 'ordn' on;
	-webkit-font-feature-settings: 'pnum' on, 'lnum' on;
	font-feature-settings: 'pnum' on, 'lnum' on;
}
h1 {
	font-weight: bold;
	font-size: var(--font-size-var1);
	line-height: 100%;
	color: var(--color-white);
	text-align: center;
}
h2 {
	font-weight: bold;
	font-size: var(--font-size-var1);
	line-height: 123%;
	color: var(--color-black);
	text-align: center;
	span {
		color: #e21837;
	}
}
sup {
	font-weight: 500;
	font-family: var(--font-second);
	font-size: 0.55em;
	top: -0.75em;
}
ul {
	padding: 0;
	margin: 0;
	list-style-type: none;
}
li {
	font-weight: normal;
	font-size: var(--font-size-var2);
	line-height: 140%;
	color: var(--color-white);
}
img {
	max-width: 100%;
}

.non-active {
	display: none;
}
.paused {
	animation-play-state: paused;
}
.running {
	animation-play-state: running;
}

.wrapper {
	overflow: hidden;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-pack: start;
	-ms-flex-pack: start;
	justify-content: flex-start;
	-webkit-box-align: center;
	-ms-flex-align: center;
	align-items: center;
	-webkit-box-orient: vertical;
	-webkit-box-direction: normal;
	-ms-flex-direction: column;
	flex-direction: column;
	min-height: 100%;
	width: 100%;
	margin: 0 auto;
	padding: 0;
}
.section {
	width: 100%;
	position: relative;
	z-index: 100;
	&__content {
		margin: 0 auto;
		padding: 0 clamp(16px, 2.3vw, 30px);
		max-width: calc(1280px + clamp(16px, 12.3vw, 30px) * 2);
		width: 100%;
	}
}

.switcher,
.switcher-mob {
	position: relative;
	margin: 0 auto;
	margin-top: 26px;
	max-width: clamp(490px, 66vw, 850px);
	display: flex;
	justify-content: space-between;
	align-items: center;
	background: var(--color-white);
	border-radius: 70px;
	padding: clamp(8px, 0.92vw, 12px) clamp(8px, 1.07vw, 14px);

	&_button {
		height: clamp(60px, 6.615vw, 86px);
		border-radius: 70px;
		padding: clamp(10px, 1.07vw, 14px) clamp(18px, 1.84vw, 24px);
		padding-left: clamp(24px, 2.54vw, 33px);
		display: flex;
		justify-content: flex-start;
		align-items: center;
		cursor: pointer;
		p {
			font-weight: normal;
			font-size: var(--font-size-var4);
			line-height: 123%;
			color: #1f1f1e;
		}
	}
	&_button1 {
		max-width: clamp(240px, 34.4vw, 446px);
		.button_icon {
			display: block;
			margin-bottom: -4px;
			max-width: clamp(50px, 6.84vw, 89px);
		}
		.button_icon-active {
			display: none;
			margin-bottom: -4px;
			max-width: clamp(50px, 6.84vw, 89px);
		}
		p {
			margin-left: clamp(10px, 2vw, 26px);
		}
	}
	&_button2 {
		max-width: clamp(200px, 28vw, 364px);
		.button_icon {
			display: block;
			max-width: clamp(14px, 1.77vw, 23px);
		}
		.button_icon-active {
			display: none;
			max-width: clamp(14px, 1.77vw, 23px);
		}
		p {
			margin-left: clamp(10px, 1.84vw, 24px);
			color: var(--color-blue);
		}
	}
}
.switcher-mob {
	margin: 0 auto;
	margin-top: 26px;
	display: none;
}
.switcher {
	.slider {
		margin: 0 clamp(8px, 0.92vw, 12px);
		width: 100%;
		position: absolute;
		right: auto;
		top: 50%;
		height: clamp(60px, 6.615vw, 86px);
		border-radius: 70px;
		padding: clamp(10px, 1.07vw, 14px) clamp(18px, 1.84vw, 24px);
		padding-left: clamp(24px, 2.54vw, 33px);
		display: flex;
		justify-content: flex-start;
		align-items: center;
		cursor: pointer;
		transition: all 0.3s linear;
		&-text {
			font-weight: normal;
			font-size: var(--font-size-var4);
			line-height: 123%;
			color: var(--color-white);
			transition: all 0.3s linear;
		}
	}
	.slider-btn1 {
		max-width: clamp(240px, 34.4vw, 446px);
		left: 0;
		transform: translate(0%, -50%);
		background: #ef7f85;
		transition: all 0.3s linear;
		.slider-text {
			margin-left: clamp(10px, 2vw, 26px);
			padding-left: clamp(50px, 6.84vw, 89px);
			position: relative;
			transition: all 0.3s linear;
			&:before {
				content: '';
				position: absolute;
				left: clamp(-26px, -2vw, -10px);
				top: 54%;
				transform: translate(0%, -50%);
				width: clamp(50px, 6.84vw, 89px);
				height: clamp(34px, 4.615vw, 60px);
				background: url('../../public/icon_peroral-active.svg');
				background-position: 50% 50%;
				background-size: contain;
				background-repeat: no-repeat;
				transition: all 0.3s linear;
			}
		}
	}
	.slider-btn2 {
		max-width: clamp(200px, 28vw, 364px);
		left: 100%;
		transform: translate(calc(-100% - clamp(8px, 0.92vw, 12px) * 2), -50%);
		background: var(--color-blue);
		transition: all 0.3s linear;
		.slider-text {
			margin-left: clamp(10px, 1.84vw, 24px);
			padding-left: clamp(14px, 1.77vw, 23px);
			position: relative;
			transition: all 0.3s linear;
			&:before {
				content: '';
				position: absolute;
				left: clamp(-24px, -1.84vw, -10px);
				top: 50%;
				transform: translate(0%, -50%);
				width: clamp(14px, 1.77vw, 23px);
				height: clamp(30px, 3.77vw, 49px);
				background: url('../../public/icon_nanoemulsion-active.svg');
				background-position: 50% 50%;
				background-size: contain;
				background-repeat: no-repeat;
				transition: all 0.3s linear;
			}
		}
	}
}

.bg_screen-top {
	display: block;
	position: absolute;
	left: 50%;
	top: 0;
	transform: rotate(180deg) translate(50%, 0);
	width: 100%;
	min-width: clamp(1200px, 100vw, 1920px);
}
.bg_screen-bottom {
	display: block;
	position: absolute;
	left: 50%;
	bottom: 0;
	transform: translate(-50%, 0);
	width: 100%;
	min-width: clamp(1200px, 100vw, 1920px);
}

._2 {
	background-color: var(--color-gray);
	.section__content {
		padding-top: clamp(40px, 4.6vw, 60px);
		padding-bottom: 4px;
		.compare__method_block-desktop {
			display: block;
		}
		.compare__method_block-mob {
			display: none;
		}
		.compare {
			&__methods {
				margin-top: 26px;
				display: flex;
				justify-content: flex-end;
				align-items: center;
				flex-direction: column;
				position: relative;
				min-height: clamp(300px, 47.1vw, 610px);
			}
			&__method {
				position: absolute;
				left: 50%;
				bottom: 0;
				transform: translate(-50%, 0);
				margin: 0 auto;
				max-width: 1220px;
				width: 100%;
				height: 100%;
				opacity: 0;
				transition: opacity 0.5s linear;
				&_gif1 {
					height: 100%;
					img {
						height: clamp(300px, 47.1vw, 610px);
						display: block;
						position: absolute;
						left: 50%;
						bottom: 0;
						transform: translate(-28%, 0);
					}
				}
				&_gif2 {
					height: 100%;
					img {
						height: clamp(300px, 47.1vw, 610px);
						display: block;
						position: absolute;
						left: 50%;
						bottom: 0;
						transform: translate(-48.3%, 0);
					}
				}
			}
			&__method1 {
				.compare__method_block {
					position: absolute;
					left: 0;
					top: 0;
					transform: translate(0, 0);
					max-width: clamp(200px, 25.38vw, 330px);
					&-text {
						margin-left: clamp(8px, 1.07vw, 14px);
						margin-top: 46px;
						margin-top: 12%;
						animation-name: Animation01;
						animation-duration: 14.015s;
						animation-iteration-count: infinite;
						&-item {
							display: flex;
							align-items: center;
							justify-content: flex-start;
							margin-bottom: 16px;
							margin-bottom: 4%;
							&:first-child {
								.compare__method_block-text-icon {
									height: clamp(30px, 3.07vw, 40px);
								}
							}
							p {
								padding-left: clamp(14px, 1.846vw, 24px);
								font-weight: 400;
								font-size: var(--font-size-var3a);
								line-height: 140%;
								color: var(--color-black);
							}
						}
						&-icon {
							flex: 0 0 clamp(3px, 0.5vw, 5px);
							height: clamp(42px, 4.3vw, 56px);
							background-color: #de616b;
							border-radius: 3px;
						}
					}
					&-bottom {
						margin-top: 26px;
						margin-top: 10%;
						font-weight: 400;
						font-size: var(--font-size-var2a);
						line-height: 140%;
						color: var(--color-black);
						animation-name: Animation01a;
						animation-duration: 14.015s;
						animation-iteration-count: infinite;
					}
					&-img {
						margin-top: 32px;
						margin-top: 8%;
						animation-name: Animation02;
						animation-duration: 14.015s;
						animation-iteration-count: infinite;
					}
				}
			}
			&__method2 {
				.compare__method-img {
					margin-top: 36px;
					margin-top: 3%;
					max-width: clamp(160px, 27.38vw, 356px);
					animation-name: Animation04;
					animation-duration: 10.055s;
					animation-iteration-count: infinite;
				}
				.compare__method_block {
					position: absolute;
					right: 0;
					top: 0;
					transform: translate(0, 0);
					max-width: clamp(200px, 31.538vw, 410px);
					&-text {
						margin-left: 10px;
						margin-top: clamp(10px, 6vw, 80px);
						margin-top: 20%;
						animation-name: Animation03;
						animation-duration: 10.055s;
						animation-iteration-count: infinite;
						&-item {
							display: flex;
							align-items: center;
							justify-content: flex-start;
							margin-bottom: 16px;
							margin-bottom: 4%;
							&:first-child {
								.compare__method_block-text-icon {
									height: clamp(54px, 4.61vw, 60px);
								}
							}
							p {
								padding-left: clamp(14px, 1.846vw, 24px);
								font-weight: 400;
								font-size: var(--font-size-var3a);
								line-height: 140%;
								color: var(--color-black);
							}
						}
						&-icon {
							flex: 0 0 clamp(3px, 0.5vw, 5px);
							height: clamp(36px, 3.46vw, 45px);
							background-color: #2eaeff;
							border-radius: 3px;
						}
					}
					&-bottom {
						margin-top: clamp(14px, 2.76vw, 36px);
						margin-top: 9%;
						font-weight: 400;
						font-size: var(--font-size-var2a);
						line-height: 140%;
						color: var(--color-black);
						animation-name: Animation03a;
						animation-duration: 10.055s;
						animation-iteration-count: infinite;
					}
				}
			}
			&__method-active {
				opacity: 1;
				transition: opacity 0.3s linear;
			}
		}
	}
}

@media (max-width: 1000px) {
	.bg_screen-top,
	.bg_screen-bottom {
		min-width: clamp(500px, 120vw, 1200px);
	}
}
@media (max-width: 768px) {
	.switcher {
		.switcher_button1 {
			max-width: clamp(265px, 34.4vw, 446px);
		}
		.switcher_button2 {
			max-width: clamp(210px, 28vw, 364px);
		}
	}
	._2 {
		.section__content {
			.compare {
				&__methods {
					margin-top: clamp(16px, 2.6vw, 20px);
					min-height: clamp(200px, 47.39vw, 364px);
				}
				&__method {
					&_gif1 {
						height: clamp(200px, 47.39vw, 364px);
					}
					&_gif2 {
						height: clamp(200px, 47.39vw, 364px);
					}
				}
			}
		}
	}
}
@media (max-width: 767px) {
	._2 {
		.section__content {
			.compare__method_block-desktop {
				display: none;
			}
			.compare__method_block-mob {
				display: block;
			}
		}
	}
}
@media (max-width: 500px) {
	.bg_screen-top,
	.bg_screen-bottom {
		min-width: 740px;
	}
	.switcher {
		display: none;
	}
	.switcher-mob {
		display: flex;
		justify-content: center;
		flex-wrap: wrap;
		background: none;
		.switcher_button {
			max-width: clamp(300px, 66vw, 330px);
			width: 100%;
			background: var(--color-white);
			border: 6px solid var(--color-white);
			height: 80px;
			box-shadow: inset 0px 0px 1px #000000;
			p {
				margin-left: clamp(10px, 3vw, 15px);
				font-size: clamp(14px, 3.2vw, 16px);
			}
		}
		.switcher_button1 {
			.button_icon {
				display: block;
				max-width: clamp(50px, 11vw, 55px);
			}
			.button_icon-active {
				display: none;
				max-width: clamp(50px, 11vw, 55px);
			}
		}
		.switcher_button1-active {
			background: #ef7f85;
			box-shadow: none;
			.button_icon {
				display: none;
			}
			.button_icon-active {
				display: block;
			}
			p {
				color: var(--color-white);
			}
		}
		.switcher_button2 {
			margin-top: 16px;
			.button_icon {
				display: block;
				max-width: clamp(13.6px, 3vw, 15px);
				margin-bottom: 4px;
			}
			.button_icon-active {
				display: none;
				max-width: clamp(13.6px, 3vw, 15px);
				margin-bottom: 4px;
			}
		}
		.switcher_button2-active {
			background: var(--color-blue);
			box-shadow: none;
			.button_icon {
				display: none;
			}
			.button_icon-active {
				display: block;
			}
			p {
				color: var(--color-white);
			}
		}
	}
	._2 {
		.section__content {
			.compare__methods {
				min-height: clamp(150px, 46vw, 230px);
			}
			.compare__method {
				transition: none;
				&_gif1 {
					height: clamp(150px, 46vw, 230px);
				}
				&_gif2 {
					height: clamp(150px, 46vw, 230px);
				}
			}
			.compare__method-active {
				transition: none;
			}
		}
	}
}
@media (max-width: 425px) {
	.bg_screen-top,
	.bg_screen-bottom {
		min-width: 600px;
	}
	._2 {
		.section__content {
			padding-top: 30px;
			.compare__methods {
				min-height: clamp(150px, 45.41vw, 193px);
			}
			.compare__method {
				&_gif1 {
					height: clamp(150px, 45.41vw, 193px);
				}
				&_gif2 {
					height: clamp(150px, 45.41vw, 193px);
				}
			}
		}
	}
}

